import React from 'react'
import { auth } from '../../firebase/firebase.utils'

import './comerceAlert.styles.scss'

const ComerceAlert = () => {
   return (
      <div className='comerce-alert'>
         <button onClick={() => auth.signOut()}><i className='fas fa-sign-out-alt'></i>Cerrar Sesion</button>
         <h3>Si quieres usar tus opciones de administrador debes ingresar desde la app para android</h3>
         <img src={require('../../img/app-draw.svg')} alt="dibujo-telefono"/>
         <a href="https://play.google.com/store/apps/details?id=com.reservawebapp"><i className='fab fa-google-play'></i>Descarga la app</a>
      </div>
   )
}

export default ComerceAlert
