import React, { useEffect, useState, useContext } from "react";

import Loader from "../../../components/loader/loader.component";
import PlaceItem from "../../../components/placeItem/placeItem.component";

import { UserContext } from "../../../context/context";

import { firestore } from "../../../firebase/firebase.utils";

import "./userPlaces.scss";

const UserPlaces = () => {
   const [loading, setLoading] = useState(true);
   const [places, setPlaces] = useState(null);

   const { currentUser } = useContext(UserContext);

   useEffect(() => {
      const subscriber = getMyPlaces();
      return () => subscriber;
      //eslint-disable-next-line
   }, []);

   const getMyPlaces = async () => {
      await firestore
         .collection(`users/${currentUser.id}/places`)
         .onSnapshot(onResult, onError);
   };

   const onResult = (querySnapshot) => {
      let placesArray = [];

      querySnapshot.forEach((documentSnapshot) => {
         placesArray.push({
            id: documentSnapshot.id,
            ...documentSnapshot.data(),
         });
      });

      setPlaces(placesArray);
      setLoading(false);
   };

   const onError = (error) => {
      console.log(error);
   };

   return (
      <div className="user-places-section">
         <h3>Selecciona el lugar a agendar</h3>
         <div className="places-list">
            {loading ? (
               <Loader />
            ) : places.length === 0 ? (
               <div className="no-places">
                  <p>Aun no tienes lugares</p>
               </div>
            ) : (
               places.map((place) => <PlaceItem key={place.id} data={place} />)
            )}
         </div>
      </div>
   );
};

export default UserPlaces;
