import React from "react";

export const checkImage = (type) => {
   if (type === "Unidad Residencial")
      return require("../img/building-default.jpg");
   else if (type === "Gimnasio") return require("../img/gym-default.jpg");
   else if (type === "Restaurante") return require("../img/rest-default.jpg");
   else if (type === "Iglesia") return require("../img/church-default.jpg");
   else if (type === "Comercio") return require("../img/comerce-default.jpg");
};

export const checkIcon = (type) => {
   if (type === "Unidad Residencial")
      return <i className="fas fa-building"></i>;
   else if (type === "Gimnasio") return <i className="fas fa-dumbbell"></i>;
   else if (type === "Restaurante") return <i className="fas fa-utensils"></i>;
   else if (type === "Iglesia") return <i className="fas fa-cross"></i>;
   else if (type === "Comercio") return <i className="fas fa-shopping-bag"></i>;
};
