import React, { useContext, useState } from "react";
import { useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import { UserContext } from "../../context/context";

import { auth } from "../../firebase/firebase.utils";

import "./userNav.styles.scss";

const UserNav = () => {
   const [showMobileNav, setShowMobileNav] = useState(false);

   const { currentUser } = useContext(UserContext);
   let location = useLocation();

   useEffect(() => {
      [(["resize", onResize], ["click", onWindowClick])].forEach((actions) => {
         window.addEventListener(actions[0], actions[1]);
      });

      return () => {
         [["resize", onResize], ["click"], onWindowClick].forEach((actions) => {
            window.removeEventListener(actions[0], actions[1]);
         });
      };
      //eslint-disable-next-line
   }, []);

   const onWindowClick = () => {
      if (showMobileNav) {
         setShowMobileNav(false);
      }
   };

   const onResize = () => {
      if (window.innerWidth > 800) {
         setShowMobileNav(false);
      }
   };

   const handleNavTouch = (event) => {
      if (window.innerWidth < 800) {
         event.stopPropagation();
         setShowMobileNav(!showMobileNav);
      } else {
         setShowMobileNav(false);
      }
   };

   const getInitials = (string) => {
      let names = string.trim().split(" "),
         initials = names[0].substring(0, 1).toUpperCase();

      if (names.length > 1) {
         initials += names[names.length - 1].substring(0, 1).toUpperCase();
      }
      return initials;
   };

   return (
      <nav className="nav-container">
         <img src={require("../../img/Reservaweb-orange.png")} alt="" />

         <div className="links">
            <Link
               to="/"
               className={location.pathname === "/app" ? "selected" : undefined}
            >
               Buscar
            </Link>
            <Link
               to="/app/saved"
               className={
                  location.pathname === "/app/saved" ? "selected" : undefined
               }
            >
               Mis Lugares
            </Link>
            <Link
               to="/app/profile"
               className={`${
                  location.pathname === "/app/profile" ? "selected" : undefined
               }`}
            >
               Perfil
            </Link>
            <p onClick={() => auth.signOut()}>Cerrar sesion</p>
            <div className="photo" onClick={handleNavTouch}>
               <i className="fas fa-caret-down"></i>
               <div className="user-photo">
                  <p>
                     {currentUser.displayName
                        ? getInitials(currentUser.displayName)
                        : "?"}
                  </p>
               </div>
            </div>
         </div>
         {showMobileNav && (
            <div id="mobile-nav">
               <p>holiguis</p>
            </div>
         )}
      </nav>
   );
};

export default UserNav;
