import React, { useState } from "react";
import { Link } from "react-router-dom";

import emailjs from "emailjs-com";

import AppIcons from "../../../components/appIcons/appIcons.component";

import "./homePage.styles.scss";
import Swal from "sweetalert2";
import { Helmet } from "react-helmet";

import { Zoom, Fade } from "react-reveal";

function HomePage() {
   const [buttonDisabled, setButtonDisabled] = useState(false);
   const [userInfo, setUserInfo] = useState({
      userName: "",
      userEmail: "",
      userCel: "",
      message: "",
   });

   console.log('im in home');

   const submitForm = (onSubmit) => {
      onSubmit.preventDefault();

      const emailRegEx = /^([0-9a-zA-Z]([-.\w]*[0-9a-zA-Z])*@([0-9a-zA-Z][-\w]*[0-9a-zA-Z]\.)+[a-zA-Z]{2,9})$/;

      if (!userInfo.userEmail.match(emailRegEx)) {
         Swal.fire({
            title: "Correo invalido",
            icon: "error",
         });
         return;
      }

      let service_id = "service_j1n81mq",
         template_id = "template_2zbmt4i",
         user_id = "user_tmIqCBdON9pvqG8IJgSuU";

      setButtonDisabled(true);
      emailjs
         .send(service_id, template_id, userInfo, user_id)
         .then(() => {
            Swal.fire({
               title: "Enviado con exito",
               icon: "success",
            });
            document.getElementById("form").reset();
            setUserInfo({
               userName: "",
               userEmail: "",
               userCel: "",
               message: "",
            });
            setButtonDisabled(false);
         })
         .catch((error) => {
            Swal.fire({
               title: "Error inesperado",
               icon: "error",
            });
            document.getElementById("form").reset();
            setButtonDisabled(false);
            console.log(error);
         });
   };

   const onInputChange = (event) => {
      const name = event.target.name;
      const value = event.target.value;

      setUserInfo((oldInfo) => ({
         ...oldInfo,
         [name]: value,
      }));
   };

   return (
      <div className="home-page">
         <Helmet>
            <title>Reserva Web || todas tus reservas en un solo lugar</title>
         </Helmet>

         <header>
            <Zoom>
               <img
                  className="home-logo"
                  src={require("../../../img/Reservaweb-white.png")}
                  alt="logo-reservaweb"
               />

               <p>Todas tus reservas en un solo lugar!</p>
            </Zoom>

            <div className="header-buttons">
               <Fade left big>
                  <a
                     className="header-button play-store-button"
                     href="https://play.google.com/store/apps/details?id=com.reservawebapp"
                     target="_blank"
                     rel="noopener noreferrer"
                  >
                     <i className="fab fa-google-play"></i>
                     Playstore
                  </a>
               </Fade>
               <Fade right big >
                  <Link className="header-button web-button" to="/signin">
                     <i className="fas fa-globe"></i>
                     Aplicacin Web
                  </Link>
               </Fade>
            </div>
         </header>

         <section className="functions-section">
            <h2>Que te ayudara a hacer.</h2>
            <Fade left>
               <div className="functions-container">
                  <div className="function">
                     <div className="function-icons">
                        <i className="fas fa-dumbbell"></i>
                        <i className="fas fa-building"></i>
                     </div>
                     <h3>Gimnasios y Unidades residenciales</h3>
                     <ul>
                        <li>
                           <p>
                              Realizar la encuesta COVID 19 a cada usuario que
                              pretenda utilizar los servicios antes de poder
                              reservar.
                           </p>
                        </li>
                        <li>
                           <p>
                              Reserva anticipada por parte de los usuarios
                              autorizados por el administrador para cada uno de
                              los servicios.
                           </p>
                        </li>
                        <li>
                           <p>
                              Llevar registro y consolidar una base de datos con
                              la informacin de las personas que utilizan cada
                              uno de los servicios
                           </p>
                        </li>
                        <li>
                           <p>
                              Definir das, horarios y duracin de ocupacin
                              para cada uno de los servicios
                           </p>
                        </li>
                     </ul>
                  </div>
                  <div className="function">
                     <div className="function-icons">
                        <i className="fas fa-utensils"></i>
                        <i className="fas fa-cross"></i>
                     </div>
                     <h3>Restaurantes y Iglesias</h3>
                     <ul>
                        <li>
                           <p>
                              Realizar la encuesta COVID 19 a cada usuario que
                              pretenda utilizar los servicios antes de poder
                              reservar.
                           </p>
                        </li>
                        <li>
                           <p>
                              Reserva anticipada por los usuarios para asistir a
                              cada uno de los servicios del establecimiento.
                           </p>
                        </li>

                        <li>
                           <p>
                              Llevar registro y consolidar una base de datos con
                              la informacin de las personas que utilizan cada
                              uno de los servicios
                           </p>
                        </li>
                     </ul>
                  </div>
                  <div className="function">
                     <div className="function-icons">
                        <i className="fas fa-shopping-bag"></i>
                     </div>
                     <h3>Comercios</h3>
                     <ul>
                        <li>
                           <p>
                              Configurar el maximo aforo permitido en el
                              establecimiento.
                           </p>
                        </li>
                        <li>
                           <p>
                              Contabilizar entrada y salida de visitantes al
                              establecimiento.
                           </p>
                        </li>
                        <li>
                           <p>
                              Llevar registro y consolidar una base de datos con
                              la informacin de las personas que utilizan cada
                              uno de los servicios
                           </p>
                        </li>
                        <li>
                           <p>
                              Visualizar en tiempo real la ocupacin del
                              establecimiento.
                           </p>
                        </li>
                     </ul>
                  </div>
               </div>
               <p className="function-description">
                  ReservaWeb esta disponible para cualquier tipo de
                  establecimiento, aqui te mostramos los principales.
               </p>
            </Fade>
         </section>

         <div className="div"></div>

         <section className="form-section">
            <div className="form-container">
               <Fade left>
                  <div className="form-text">
                     <p>
                        Necesitas llevar un control para la reapertura de tu
                        establecimiento?
                     </p>
                     <p>
                        Contactanos y te daremos mas informacion sobre la
                        aplicacin
                     </p>
                  </div>
               </Fade>
               <Fade right>
                  <form onSubmit={submitForm} id="form">
                     <label htmlFor="name">Nombre Completo: </label>
                     <input
                        type="text"
                        name="userName"
                        id="name"
                        maxLength={40}
                        onChange={onInputChange}
                        value={userInfo.userName}
                        required
                     />

                     <label htmlFor="email">Email: </label>
                     <input
                        type="email"
                        name="userEmail"
                        id="email"
                        minLength={20}
                        maxLength={40}
                        onChange={onInputChange}
                        value={userInfo.userEmail}
                        required
                     />

                     <label htmlFor="cel">Celular: </label>
                     <input
                        type="text"
                        name="userCel"
                        id="cel"
                        minLength={10}
                        maxLength={10}
                        onChange={onInputChange}
                        value={userInfo.userCel}
                        required
                     />

                     <label htmlFor="message">Mensaje: </label>
                     <textarea
                        name="message"
                        id="message"
                        rows="3"
                        maxLength={100}
                        onChange={onInputChange}
                        value={userInfo.message}
                     ></textarea>
                     <input
                        type="submit"
                        value={buttonDisabled ? "Espere..." : "Enviar"}
                        disabled={buttonDisabled}
                     />
                  </form>
               </Fade>
            </div>
         </section>
         <AppIcons />
      </div>
   );
}

export default HomePage;
