import React from "react";
import { QRCode } from "react-qr-svg";

import "./qrCode.styles.scss";

const QrCode = ({ currentUser, setShowQr }) => {
   return (
      <div className="qr-code-page">
         <div></div>
         <div className="qr-code-container">
            <QRCode
               className="qr-code"
               bgColor="#FFFFFF"
               fgColor="#000000"
               value={`${currentUser.id},${currentUser.displayName},${currentUser.email},${currentUser.cel},${currentUser.docId}`}
            />
            <button onClick={() => setShowQr(false)}>cerrar</button>
         </div>
         <p>
            Te recomendamos que tomes captura de pantalla de tu Codigo Qr para
            que lo uses sin conexin a internet
         </p>
      </div>
   );
};

export default QrCode;
