import React, { useContext, useEffect, useState } from "react";
import "./joinRequest.styles.scss";

import { firestore } from "../../firebase/firebase.utils";

import { UserContext } from "../../context/context";
import Swal from "sweetalert2";
import { useHistory } from "react-router-dom";
import Loader from "../loader/loader.component";

const JoinRequest = ({ placeInfo }) => {
   const [apt, setApt] = useState(null);
   const [section, setSection] = useState("askforrequest");
   const { currentUser } = useContext(UserContext);
   const [loading, setLoading] = useState(true);

   const history = useHistory();

   const estRequestRef = firestore.doc(
      `establishments/${placeInfo.id}/requests/${currentUser.id}`
   );
   const estUsersRef = firestore.doc(
      `establishments/${placeInfo.id}/users/${currentUser.id}`
   );

   useEffect(() => {
      const subscriber = getStatus();

      return () => subscriber;
      //eslint-disable-next-line
   }, []);

   async function getStatus() {
      setLoading(true);
      await estUsersRef.onSnapshot(
         (snapShot) => {
            if (snapShot.exists) {
               setSection("accepted");
               setLoading(false);
            } else {
               estRequestRef.onSnapshot(
                  (snapShot) => {
                     if (snapShot.exists) {
                        setSection("requested");
                        setLoading(false);
                     } else {
                        setSection("askforrequest");
                        setLoading(false);
                     }
                  },
                  (error) => console.log(error)
               );
            }
         },
         (error) => console.log(error)
      );
   }

   const sendApt = async () => {
      if (!apt || apt === 0 || apt === "0") {
         Swal.fire({
            title: "Escriba su apartamento",
            icon: "warning",
         });

         return;
      }
      if (currentUser.ready) {
         await estRequestRef
            .set({
               apt: apt,
               ...currentUser,
            })
            .then(() => {});
      } else {
         Swal.fire({
            title:
               "Antes de ingresar a un establecimiento debes completar tu perfil",
            icon: "warning",
         });
         history.replace("/app/profile");
      }
   };

   const sendGym = async () => {
      if (currentUser.ready) {
         setLoading(true);
         await estRequestRef
            .set({
               ...currentUser,
            })
            .then(() => {
               setLoading(false);
            });
      } else {
         Swal.fire({
            title:
               "Antes de ingresar a un establecimiento debes completar tu perfil",
            icon: "warning",
         });
         history.replace("/app/profile");
      }
   };

   return (
      <div className="join-request-section">
         {loading ? (
            <Loader />
         ) : section === "askforrequest" ? (
            <button
               className="ask-for-request"
               onClick={() =>
                  placeInfo.type === "Unidad Residencial"
                     ? setSection("aptrequest")
                     : sendGym()
               }
            >
               <i className="fas fa-user-home"></i>
               Solicitar Unirse
            </button>
         ) : section === "aptrequest" ? (
            <div className="apt-section">
               <p>Ingrese el apartamento en el que reside</p>
               <input
                  className="apt-input"
                  type="text"
                  placeholder="EJ: 408B"
                  maxLength={6}
                  onChange={(event) => setApt(event.target.value)}
               />
               <button className="apt-send" onClick={sendApt}>
                  Enviar solicitud
               </button>
            </div>
         ) : section === "requested" ? (
            <p className="requested">
               <i className="fas fa-paper-plane"></i>
               Ya se ha enviado tu solicitud
            </p>
         ) : (
            <p className="accepted">
               <i className="fas fa-check"></i>
               Ya eres usuario de este establecimiento, puedes agendar horarios
               desde "Mis Lugares"
            </p>
         )}
      </div>
   );
};

export default JoinRequest;
