import React, { useContext } from "react";
import { useHistory, useRouteMatch } from "react-router-dom";
import { UserContext } from "../../context/context";
import { checkIcon, checkImage } from "../../utils/checkImageIcon";

import "./placeItem.styles.scss";

const PlaceItem = ({ data }) => {
   const { name, type, image, to } = data;

   const { setPlaceInfo } = useContext(UserContext);

   const history = useHistory();
   const { path } = useRouteMatch();

   return (
      <div className="item-container">
         <img src={image ? image : checkImage(type)} alt="imagen-lugar" />
         <div className="text-container">
            <p>{name}</p>
            <p className="type">
               {checkIcon(type)}
               {type}
            </p>
            <button
               onClick={() => {
                  history.push(path + to);
                  setPlaceInfo(data);
               }}
            >
               Ver Ms
            </button>
         </div>
      </div>
   );
};

export default PlaceItem;
