import React, { useEffect, useState } from "react";

import {
   Route,
   BrowserRouter as Router,
   Switch,
   Redirect,
} from "react-router-dom";

import Swal from "sweetalert2";

import "./App.css";
import { auth, createUserProfileDocument } from "./firebase/firebase.utils";
import ForgotPassword from "./pages/anonymousRoutes/forgotPassword/forgotPassword.component";
import HomePage from "./pages/anonymousRoutes/homePage/homePage.component";
import SignIn from "./pages/anonymousRoutes/signIn/signIn.component";
import SignUp from "./pages/anonymousRoutes/signUp/signUp.component";

import { GlobalContext } from "./context/context";
import TransLoader from "./components/transLoader/transLoader.component";
import UserRouter from "./components/userRouter";
import ComerceAlert from "./components/comerceAlert/comerceAlert.component";
import AdminRouter from "./components/adminRouter";

function App() {
   const [isAdminAuthenticated, setIsAdminAuthenticated] = useState(false);
   const [isUserAuthenticated, setIsUserAuthenticated] = useState(false);
   const [showTransLoader, setShowTransLoader] = useState(false);

   useEffect(() => {
      const subscriber = authState();

      return () => subscriber;
   }, []);

   const authState = () => {
      auth.onAuthStateChanged(async (userAuth) => {
         setShowTransLoader(true);
         if (!userAuth) {
            setIsAdminAuthenticated(false);
            setIsUserAuthenticated(false);
            setShowTransLoader(false);
         } else if (!userAuth.emailVerified) {
            setShowTransLoader(false);
            Swal.fire({
               title: "Para usar la aplicacin confirma tu correo electronico",
               text:
                  "Si ya confirmaste tu correo y no puedes ingresar por favor cierra la web y abrela nuevamente",
               icon: "info",
            });
         } else if (userAuth && userAuth.emailVerified) {
            const userRef = await createUserProfileDocument(userAuth);

            userRef.onSnapshot((snapShot) => {
               if (snapShot.data().admin) {
                  setIsAdminAuthenticated({
                     id: snapShot.id,
                     ...snapShot.data(),
                  });
               } else {
                  setIsUserAuthenticated({
                     id: snapShot.id,
                     ...snapShot.data(),
                  });
               }
            });
            setShowTransLoader(false);
         }
      });
   };

   const CheckAuth = ({ children, ...rest }) => {
      return (
         <Route
            {...rest}
            render={({ location }) =>
               isUserAuthenticated ? (
                  <Redirect
                     to={{ pathname: "/app", state: { from: location } }}
                  />
               ) : isAdminAuthenticated ? (
                  <Redirect
                     to={{ pathname: "/admin", state: { from: location } }}
                  />
               ) : children
            }
         />
      );
   };

   const PrivateUser = ({ children, ...rest }) => {
      return (
         <Route
            {...rest}
            render={({ location }) =>
               isUserAuthenticated ? (
                  children
               ) : (
                  <Redirect
                     to={{
                        pathname: "/signin",
                        state: { from: location },
                     }}
                  />
               )
            }
         />
      );
   };

   const PrivateAdmin = ({ children, ...rest }) => {
      return (
         <Route
            {...rest}
            render={({ location }) =>
               isAdminAuthenticated ? (
                  children
               ) : (
                  <Redirect
                     to={{
                        pathname: "/signin",
                        state: { from: location },
                     }}
                  />
               )
            }
         />
      );
   };

   return (
      <div className="App">
         <GlobalContext.Provider value={{ setShowTransLoader }}>
            <Router>
               <Switch>
                  <CheckAuth exact path="/">
                     <HomePage />
                  </CheckAuth>
                  <CheckAuth exact path="/signin">
                     <SignIn />
                  </CheckAuth>
                  <CheckAuth exact path="/signup">
                     <SignUp />
                  </CheckAuth>
                  <CheckAuth exact path="/forgot-password">
                     <ForgotPassword />
                  </CheckAuth>
                  <PrivateUser path="/app">
                     <UserRouter currentUser={isUserAuthenticated} />
                  </PrivateUser>
                  <PrivateAdmin path="/admin">
                     <AdminRouter currentUser={isAdminAuthenticated} />
                  </PrivateAdmin>
                  <PrivateAdmin exact path="/comerce">
                     <ComerceAlert currentUser={isAdminAuthenticated} />
                  </PrivateAdmin>
               </Switch>
            </Router>
         </GlobalContext.Provider>
         {showTransLoader && <TransLoader />}
      </div>
   );
}

export default App;
