import React, { useEffect } from "react";
import { useContext } from "react";
import { Helmet } from "react-helmet";
import { useHistory } from "react-router-dom";
import ComercePreview from "../../../components/comercePreview/comercePreview.component";
import JoinRequest from "../../../components/joinRequest/joinRequest.component";
import { UserContext } from "../../../context/context";
import { checkIcon, checkImage } from "../../../utils/checkImageIcon";

import "./placeInfo.scss";

const PlaceInfo = () => {
   const history = useHistory();

   const { placeInfo } = useContext(UserContext);

   useEffect(() => {
      const subscriber = !placeInfo && history.replace("/app");

      return () => subscriber;
      //eslint-disable-next-line
   }, []);

   const { name, image, type } = placeInfo;

   return (
      <div className="place-info">
         <Helmet>
            <title>{`Informacion de ${name}`} || Reserva Web</title>
         </Helmet>
         <img
            src={image ? image : checkImage(type)}
            alt="imagen-establecimiento"
         />
         <p className="title">
            {checkIcon(type)}
            {name}
         </p>

         {type === "Unidad Residencial" || type === "Gimnasio" ? (
            <JoinRequest placeInfo={placeInfo} />
         ) : type === "Comercio" ? (
            <ComercePreview placeInfo={placeInfo}/>
         ) : null}
      </div>
   );
};

export default PlaceInfo;
