import React, { useEffect, useState } from "react";
import "./searchPlaces.scss";

import { firestore } from "../../../firebase/firebase.utils";
import Loader from "../../../components/loader/loader.component";
import PlaceItem from "../../../components/placeItem/placeItem.component";

const SearchPlaces = () => {
   const [iconPressed, setIconPressed] = useState(null);
   const [loading, setLoading] = useState(true);
   const [establishments, setEstablishments] = useState([]);
   const [filterByName, setFilterByName] = useState("");

   useEffect(() => {
      const subscriber = getEstablishments();

      // cleanup function
      return () => subscriber;
      //eslint-disable-next-line
   }, []);

   async function getEstablishments() {
      await firestore
         .collection("establishments")
         .where("available", "==", true)
         .orderBy("name")
         .onSnapshot(onResult, onError);
   }

   const onResult = (QuerySnapshot) => {
      const establishments = [];

      QuerySnapshot.forEach((documentSnapshot) => {
         establishments.push({
            ...documentSnapshot.data(),
            id: documentSnapshot.id,
         });
      });
      setEstablishments(establishments);
      setLoading(false);
   };

   const onError = (error) => {
      console.log(error);
   };

   const handleFilterIcon = (icon) => {
      if (icon === iconPressed) {
         setIconPressed(null);
      } else {
         setIconPressed(icon);
      }
   };

   const filterByIcon = (obj) => {
      if (
         iconPressed === null &&
         obj.name
            .toLowerCase()
            .trim()
            .includes(filterByName.toLowerCase().trim())
      )
         return true;
      else if (
         iconPressed === obj.type &&
         obj.name
            .toLowerCase()
            .trim()
            .includes(filterByName.toLowerCase().trim())
      )
         return true;
      else return false;
   };

   return (
      <div className="search-places-section">
         <div className="search-container">
            <input
               type="text"
               placeholder="Busca lugares"
               value={filterByName}
               onChange={(event) => setFilterByName(event.target.value)}
            />
            <i className="fas fa-search" />
         </div>

         <div className="icons-container">
            <i
               onClick={() => handleFilterIcon("Gimnasio")}
               className={`fas fa-dumbbell ${
                  iconPressed === "Gimnasio" && "pressed"
               }`}
            />
            <i
               onClick={() => handleFilterIcon("Restaurante")}
               className={`fas fa-utensils ${
                  iconPressed === "Restaurante" && "pressed"
               }`}
            />
            <i
               onClick={() => handleFilterIcon("Iglesia")}
               className={`fas fa-cross ${
                  iconPressed === "Iglesia" && "pressed"
               }`}
            />
            <i
               onClick={() => handleFilterIcon("Unidad Residencial")}
               className={`fas fa-building ${
                  iconPressed === "Unidad Residencial" && "pressed"
               }`}
            />
            <i
               onClick={() => handleFilterIcon("Comercio")}
               className={`fas fa-shopping-bag ${
                  iconPressed === "Comercio" && "pressed"
               }`}
            />
         </div>

         <div className="places-container">
            {loading ? (
               <Loader />
            ) : (
               establishments
                  .filter(filterByIcon)
                  .map((establishment) => (
                     <PlaceItem key={establishment.id} data={{...establishment, to:'/info'}} />
                  ))
            )}
         </div>
      </div>
   );
};

export default SearchPlaces;
