import React, { useState } from 'react'
import Swal from 'sweetalert2'
import CustomInput from '../../../components/customInput/customInput.component'
import { auth } from '../../../firebase/firebase.utils'

import './forgotPassword.styles.scss'

import { useHistory } from 'react-router-dom'
import { Helmet } from 'react-helmet'

const ForgotPassword = () => {

   const history = useHistory()

   const [ email, setEmail ] = useState('')

   const onSend = () => {
      const emailRegEx = /^([0-9a-zA-Z]([-.\w]*[0-9a-zA-Z])*@([0-9a-zA-Z][-\w]*[0-9a-zA-Z]\.)+[a-zA-Z]{2,9})$/;

      if(email.match(emailRegEx)){

         auth.sendPasswordResetEmail(email)
         .then(() => {
            Swal.fire({
               title: 'Te hemos enviado un correo para restablecer tu contrasea',
               icon: 'success'
            })
            history.replace('/signin')
         })
         .catch(err => {
            if(err.code === 'auth/user-not-found'){
               Swal.fire({
                  title: 'Este correo no esta registrado',
                  icon: 'error'
               })
            }else{
               console.log(err.code)
            }
         })
      }
      else{
         Swal.fire({
            title: 'Email invalido',
            icon: 'error'
         })
      }

   }

   return (
      <div className='forgot-password-page'>
         <Helmet>
            <title>Recupera tu contrasea || Reserva Web</title>
         </Helmet>

         <h2>Recupera tu contrasea</h2>

         <img src={require('../../../img/forgot-password.svg')} alt="juas"/>

         <p>Escribe tu email y te enviaremos un correo de restauracin de tu contrasea</p>
         
         <CustomInput 
            name='email'
            type='email'
            value={email}
            onChange={(event) => setEmail(event.target.value)}
            label='Email: '
            required
         />


         <button onClick={onSend}>Enviar</button>
      </div>
   )
}

export default ForgotPassword
