import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import { AdminContext } from "../context/context";
import { firestore } from "../firebase/firebase.utils";
import Loader from "./loader/loader.component";

const AdminRouter = ({ currentUser }) => {
   const [estInfo, setEstInfo] = useState(null);
   const [loading, setLoading] = useState(true);

   useEffect(() => {
      const subscriber = getEstInfo();

      return () => subscriber;
      //eslint-disable-next-line
   }, []);

   const getEstInfo = async () => {
      await firestore
         .doc(`establishments/${currentUser.est}`)
         .onSnapshot(onResult, onError);
   };

   const onResult = (data) => {
      setEstInfo({
         id: data.id,
         ...data.data(),
      });
      setLoading(false);
   };

   const onError = (error) => {
      console.log(error);
   };

   const CheckIfComerce = ({ children, ...rest }) => {
      return (
         <Route
            {...rest}
            render={({ location }) =>
               estInfo.type === "Comercio" ? (
                  <Redirect
                     to={{
                        pathname: "/comerce",
                        state: { from: location },
                     }}
                  />
               ) : (
                  children
               )
            }
         />
      );
   };

   if (loading)
      return (
         <div style={{
            flex: 1,
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            height: '100vh'
         }}>
            <Loader />
         </div>
      );
   return (
      <AdminContext.Provider value={{estInfo, currentUser}}>
         <CheckIfComerce>
            <p>holi</p>
         </CheckIfComerce>
      </AdminContext.Provider>
   );
};

export default AdminRouter;
