import React, { useEffect, useState } from "react";
import "./comercePreview.styles.scss";
import { firestore } from "../../firebase/firebase.utils";

import Circle from "react-circle";
import Loader from "../loader/loader.component";

const ComercePreview = ({ placeInfo }) => {
   const [maximumCapacity, setMaximumCapacity] = useState(0);
   const [currentCapacity, setCurrentCapacity] = useState(0);
   const [loading, setLoading] = useState(true);

   const estCapacityRef = firestore.doc(`establishments/${placeInfo.id}`);

   useEffect(() => {
      const subscriber = getCapacity();

      return () => subscriber;
      //eslint-disable-next-line
   }, []);

   async function getCapacity() {
      setLoading(true);
      estCapacityRef.onSnapshot((documentSnapshot) => {
         setMaximumCapacity(documentSnapshot.data().max);
         setCurrentCapacity(documentSnapshot.data().capacity);
         setLoading(false);
      }, onError);
   }
   const onError = (error) => {
      console.log(error);
   };

   const getCapacityColor = () => {
      const percentage = getPercentage();
      if (percentage < 40 || percentage === 0) return "green";
      else if (percentage >= 40 && percentage < 80) return "#ffbf00";
      else if (percentage >= 80 && percentage < 98) return "#e88a12";
      else return "red";
   };

   const getPercentage = () => {
      return parseInt(
         (parseInt(currentCapacity) * 100) / parseInt(maximumCapacity)
      );
   };

   if (loading) return <Loader />;
   else
      return (
         <div className="comerce-container">
            <div className="circle">
               <Circle
                  animate={true} // Boolean: Animated/Static progress
                  animationDuration=".2s" //String: Length of animation
                  responsive={true} // Boolean: Make SVG adapt to parent size
                  lineWidth={14} // Number: Defines the thickness of the circle's stroke.
                  progress={getPercentage()} // Number: Update to change the progress and percentage.
                  progressColor={getCapacityColor()} // String: Color of "progress" portion of circle.
                  bgColor="#00000010" // String: Color of "empty" portion of circle.
                  textColor={getCapacityColor()} // String: Color of percentage text color.
                  percentSpacing={10} // Number: Adjust spacing of "%" symbol and number.
                  roundedStroke={true} // Boolean: Rounded/Flat line ends
                  showPercentage={true} // Boolean: Show/hide percentage.
                  showPercentageSymbol={true} // Boolean: Show/hide only the "%" symbol.
               />
            </div>
            <p style={{ color: getCapacityColor() }}>
               {currentCapacity}/{maximumCapacity}
            </p>
            <p style={{ color: getCapacityColor() }}>Aforo actual</p>
         </div>
      );
};

export default ComercePreview;
