import React, { useState } from "react";

import { Route, Switch, useRouteMatch } from "react-router-dom";
import { UserContext } from "../context/context";
import PlaceInfo from "../pages/userRoutes/placeInfo/placeInfo";
import UserProfile from "../pages/userRoutes/profile/userProfile";
import SearchPlaces from "../pages/userRoutes/searchPlaces/searchPlaces";

import UserPlaces from "../pages/userRoutes/userPlaces/userPlaces";
import UserNav from "./userNav/userNav.component";

const UserRouter = ({ currentUser }) => {
   const { path } = useRouteMatch();
   const [placeInfo, setPlaceInfo] = useState(null);

   return (
      <UserContext.Provider value={{ currentUser, setPlaceInfo, placeInfo }}>
         <UserNav />
         <div className="section-container">
            <div className="section">
               <Switch>
                  <Route exact path={path}>
                     <SearchPlaces />
                  </Route>
                  <Route path={`${path}/profile`}>
                     <UserProfile />
                  </Route>
                  <Route path={`${path}/saved`}>
                     <UserPlaces />
                  </Route>
                  <Route path={`${path}/info`}>
                     <PlaceInfo />
                  </Route>
               </Switch>
            </div>
         </div>
      </UserContext.Provider>
   );
};

export default UserRouter;
