import React, { useState, useContext } from "react";
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";
import SignUpSvg from "../../../components/signUpSvg/signUpSvg.component";

import CustomInput from "../../../components/customInput/customInput.component";

import "./signIn.styles.scss";
import { auth, signInWithGoogle } from "../../../firebase/firebase.utils";
import Swal from "sweetalert2";
import { GlobalContext } from "../../../context/context";

function SignIn() {
   const [showPassword, setShowPassword] = useState(false);
   const [loginInfo, setLoginInfo] = useState({
      email: "",
      userPassword: "",
   });

   const { setShowTransLoader } = useContext(GlobalContext);

   const validationSuccess = () => {
      setShowPassword(false);
      setShowTransLoader(true);
      auth
         .signInWithEmailAndPassword(loginInfo.email, loginInfo.userPassword)
         .then(() => {
            setShowTransLoader(false);
         })
         .catch((err) => {
            if (err.code === "auth/wrong-password") {
               Swal.fire({
                  title: "Contrasea Incorrecta",
                  icon: "error",
               });
            } else if (err.code === "auth/too-many-requests") {
               Swal.fire({
                  title:
                     "Has realizado demasiados intentos, intentalo mas tarde",
                  icon: "error",
               });
            } else if (err.code === "auth/network-request-failed") {
               Swal.fire({
                  title: "No hay conexion a internet",
                  icon: "error",
               });
            } else if (err.code === "auth/user-not-found") {
               Swal.fire({
                  title: "Este email no esta registrado",
                  icon: "error",
               });
            } else {
               console.log(err.code);
            }
            setShowTransLoader(false);
         });
   };

   const validateData = (submit) => {
      submit.preventDefault();

      const { email, userPassword } = loginInfo;

      const emailRegEx = /^([0-9a-zA-Z]([-.\w]*[0-9a-zA-Z])*@([0-9a-zA-Z][-\w]*[0-9a-zA-Z]\.)+[a-zA-Z]{2,9})$/;

      if (email.match(emailRegEx)) {
         if (userPassword.length >= 8 && userPassword.length <= 20) {
            validationSuccess();
         } else {
            Swal.fire({
               title: "La contrasea debe tener entre 8 a 20 caracteres.",
               icon: "warning",
            });
         }
      } else {
         Swal.fire({
            title: "Email no valido.",
            icon: "warning",
         });
      }
   };

   const onInputChange = (event) => {
      const name = event.target.name;
      const value = event.target.value;

      setLoginInfo((oldInfo) => ({
         ...oldInfo,
         [name]: value,
      }));
   };

   return (
      <div className="sign-in-page">
         <Helmet>
            <meta charSet="utf-8" />
            <title>Inicio de Sesin || Reserva Web</title>
            <link rel="canonical" href="http://mysite.com/example" />
         </Helmet>

         <div className="sign-in-form">
            <h2>Inicia Sesin</h2>
            <form onSubmit={validateData}>
               <CustomInput
                  type="email"
                  name="email"
                  value={loginInfo.email}
                  onChange={onInputChange}
                  label="Email: "
                  required
               />
               <CustomInput
                  type={showPassword ? "text" : "password"}
                  name="userPassword"
                  value={loginInfo.userPassword}
                  onChange={onInputChange}
                  label="Contrasea: "
                  required
               />

               {showPassword ? (
                  <p onClick={() => setShowPassword(false)}>
                     <i className="fas fa-eye-slash"></i>
                     Ocultar Contrasea
                  </p>
               ) : (
                  <p onClick={() => setShowPassword(true)}>
                     <i className="fas fa-eye"></i>
                     Mostrar Contrasea
                  </p>
               )}

               <input type="submit" value="Iniciar Seson" />
               <Link to="/forgot-password" className="forgot-password">
                  Olvidaste tu contrasea?
               </Link>
            </form>

            <div className="separator">
               <div></div>
               <p>O conectate con</p>
               <div></div>
            </div>

            <button className="google-button" onClick={signInWithGoogle}>
               <i className="fab fa-google"></i>
               Inicia sesin con Google
            </button>
         </div>

         <div className="sign-up-redirect">
            <h2>
               No tienes una cuenta? <br /> Registrate
            </h2>
            <div className="img">
               <SignUpSvg />
            </div>
            <Link to="/signup" className="sign-up-button">
               Registrarse
            </Link>
         </div>
      </div>
   );
}

export default SignIn;
