import React, { useContext, useState } from "react";
import QrCode from "../../../components/qrCode/qrCode.component";
import { UserContext } from "../../../context/context";

import "./userProfile.scss";

const UserProfile = () => {
   const [showQr, setShowQr] = useState(false);

   const { currentUser } = useContext(UserContext);

   return (
      <>
         <div className="profile-section">
            {!currentUser.ready && (
               <p className="edit-profile">
                  Editar Perfil<i className="fas fa-user-edit"></i>
               </p>
            )}

            <p className="info-section">
               <span>nombre:</span>
               {currentUser.displayName
                  ? currentUser.displayName
                  : "No has proporcionado un Nombre"}
            </p>
            <p className="info-section">
               <span>email:</span>
               {currentUser.email}
            </p>
            <p className="info-section">
               <span>documento de identidad:</span>
               {currentUser.docId
                  ? currentUser.docId
                  : "No has proporcionado un Documento"}
            </p>
            <p className="info-section">
               <span>celular:</span>
               {currentUser.cel
                  ? currentUser.cel
                  : "No has proporcionado un Celular"}
            </p>

            <button
               disabled={!currentUser.ready}
               onClick={() => setShowQr(true)}
            >
               <i className="fas fa-qrcode"></i>
               {currentUser.ready ? "Ve tu Qr" : "Completa tu perfil"}
            </button>
            <a
               href="https://wa.me/573044285676?text=Hola"
               target="_blank"
               rel="noopener noreferrer"
               className="whatsapp"
            >
               <i className="fab fa-whatsapp"></i>
               Contactanos
            </a>

            {!currentUser.ready && (
               <p className="not-ready-text">
                  Para ver tu Qr primero debes completar tu perfil
               </p>
            )}
         </div>
         {showQr && <QrCode currentUser={currentUser} setShowQr={setShowQr} />}
      </>
   );
};

export default UserProfile;
