import React, { useEffect, useRef } from 'react'
import { Link } from 'react-router-dom'

import './appIcons.styles.scss'

export default function AppIcons() {

   const appIcons = useRef(null)

   useEffect(() => {
      ['scroll', 'pointerover', 'resize', 'load'].forEach(evt =>
         window.addEventListener(evt, showHideAppIcons)
      )

      return () => {
         ['scroll', 'pointerover', 'resize', 'load'].forEach(evt =>
            window.removeEventListener(evt, showHideAppIcons)
         )
      }
   }, [])

   const showHideAppIcons = () => {
      let currentScrollPos = window.pageYOffset;

      // 20 is an arbitrary number here, just to make you think if you need the prevScrollpos variable:
      if (currentScrollPos < window.innerHeight) {
         appIcons.current.style.bottom = '-35%'
      } else {
         appIcons.current.style.bottom = '2%'
      }
   }

   return (
      <div className='app-icons' ref={appIcons}>
         <a className='playstore-icon icon-container' href='https://play.google.com/store/apps/details?id=com.reservawebapp' target='_blank' rel='noopener noreferrer'>
            <i className="fab fa-google-play"></i>
            <p>Play Store</p>
         </a>

         <Link to='/signin' className='web-icon icon-container'>
            <p>Aplicacion web</p>
            <i className="fas fa-globe"></i>
         </Link>
      </div>
   )
}
