import React, { useState, useContext } from "react";
import { Helmet } from "react-helmet";
import Swal from "sweetalert2";
import CustomInput from "../../../components/customInput/customInput.component";
import SignInSvg from "../../../components/signInSvg/signInSvg.component";

import { auth, signInWithGoogle } from "../../../firebase/firebase.utils";
import { useHistory } from "react-router-dom";

import "./signUp.styles.scss";
import { GlobalContext } from "../../../context/context";

function SignUp() {
   const history = useHistory();

   const [showPassword, setShowPassword] = useState(false);
   const [registerInfo, setRegisterInfo] = useState({
      email: "",
      userPassword: "",
      confirmPassword: "",
   });

   const { setShowTransLoader } = useContext(GlobalContext);

   const validationSuccess = () => {
      setShowPassword(false);
      setShowTransLoader(true);
      auth
         .createUserWithEmailAndPassword(
            registerInfo.email,
            registerInfo.userPassword
         )
         .then((data) => {
            if (data.user) {
               data.user
                  .sendEmailVerification()
                  .then(() => {
                     setShowTransLoader(false);
                     Swal.fire({
                        title:
                           "Registrado con exito, enviamos un email para confirmar su identidad.",
                        icon: "success",
                     });
                  })
                  .catch((err) => {
                     console.log(err);
                  });
            }
         })
         .catch((err) => {
            setShowTransLoader(false);
            if (err.code === "auth/email-already-in-use") {
               Swal.fire({
                  title: "Este correo ya esta en uso",
                  icon: "error",
               });
            } else if (err.code === "auth/too-many-requests") {
               Swal.fire({
                  title:
                     "Has realizado demasiados intentos, intentalo mas tarde",
                  icon: "error",
               });
            } else if (err.code === "auth/invalid-email") {
               Swal.fire({
                  title: "Correo Invalido",
                  icon: "error",
               });
            } else {
               console.log(err.code);
            }
         });
   };

   const validateData = (submit) => {
      submit.preventDefault();
      const { userPassword, confirmPassword, email } = registerInfo;

      const emailRegEx = /^([0-9a-zA-Z]([-.\w]*[0-9a-zA-Z])*@([0-9a-zA-Z][-\w]*[0-9a-zA-Z]\.)+[a-zA-Z]{2,9})$/;

      if (email.match(emailRegEx)) {
         if (userPassword.length >= 8 && userPassword.length <= 20) {
            if (userPassword === confirmPassword) {
               validationSuccess();
            } else {
               Swal.fire({
                  title: "Las contraseas no coinciden.",
                  icon: "warning",
               });
            }
         } else {
            Swal.fire({
               title: "La contrasea debe tener entre 8 a 20 caracteres.",
               icon: "warning",
            });
         }
      } else {
         Swal.fire({
            title: "Email no valido.",
            icon: "warning",
         });
      }
   };

   const onInputChange = (event) => {
      const name = event.target.name;
      const value = event.target.value;

      setRegisterInfo((oldInfo) => ({
         ...oldInfo,
         [name]: value,
      }));
   };

   return (
      <div className="sign-up-page">
         <Helmet>
            <meta charSet="utf-8" />
            <title>Registro || Reserva Web</title>
         </Helmet>
         <div className="sign-in-form">
            <h2>Registrate</h2>
            <form onSubmit={validateData}>
               <CustomInput
                  name="email"
                  type="email"
                  value={registerInfo.email}
                  label="Email: "
                  onChange={onInputChange}
                  required
               />
               <CustomInput
                  type={showPassword ? "text" : "password"}
                  name="userPassword"
                  value={registerInfo.userPassword}
                  label="Contrasea: "
                  onChange={onInputChange}
                  required
               />
               <CustomInput
                  name="confirmPassword"
                  type={showPassword ? "text" : "password"}
                  value={registerInfo.confirmPassword}
                  label="Confirmar contrasea: "
                  onChange={onInputChange}
                  required
               />
               {showPassword ? (
                  <p onClick={() => setShowPassword(false)}>
                     <i className="fas fa-eye-slash"></i>
                     Ocultar Contraseas
                  </p>
               ) : (
                  <p onClick={() => setShowPassword(true)}>
                     <i className="fas fa-eye"></i>
                     Mostrar Contraseas
                  </p>
               )}

               <input type="submit" value="Registrarse" />
            </form>

            <div className="separator">
               <div></div>
               <p>O conectate con</p>
               <div></div>
            </div>

            <button className="google-button" onClick={signInWithGoogle}>
               <i className="fab fa-google"></i>
               Inicia sesin con Google
            </button>
         </div>

         <div className="sign-up-redirect">
            <h2>
               Ya tienes una cuenta? <br /> Inicia Sesin
            </h2>
            <div className="img">
               <SignInSvg />
            </div>
            <p onClick={() => history.goBack()} className="sign-up-button">
               Iniciar Sesin
            </p>
         </div>
      </div>
   );
}

export default SignUp;
